import myEventBus from '@/assets/js/bus';
import contentdetail from '../components/contentdetail';
import ganxingqu from '../components/ganxingqu';
import { ref, toRefs, reactive, onMounted, markRaw } from 'vue';
export default {
  name: 'xiaoxicontent',
  components: {
    contentdetail,
    ganxingqu
  },
  setup() {
    const emitter = myEventBus();
    const refItem = ref(null);
    const data = reactive({
      list: [{
        path: '/components/contentdetail',
        component: markRaw(contentdetail)
      }, {
        path: '/components/ganxingqu',
        component: markRaw(ganxingqu)
      }]
    });
    const swipeChange = i => {
      emitter.emit('slide', i);
      window.scrollTo(0, 0);
    };
    onMounted(() => {
      emitter.on('changeTab', index => {
        refItem.value.swipeTo(index);
      });
    });
    return {
      ...toRefs(data),
      refItem,
      swipeChange
    };
  }
};