import myEventBus from '@/assets/js/bus';
import { toRefs, reactive, onMounted } from 'vue';
export default {
  name: 'xiaoxiheader',
  setup() {
    const emitter = myEventBus();
    const data = reactive({
      xiaoxili: [{
        id: 'x001',
        name: '聊天'
      }, {
        id: 'x002',
        name: '互动'
      }],
      num: 0
    });
    const xiaoxiclick = index => {
      data.num = index;
      emitter.emit('changeTab', index);
      window.scrollTo(0, 0);
    };
    onMounted(() => {
      emitter.on('slide', realIndex => {
        data.num = realIndex;
      });
    });
    return {
      ...toRefs(data),
      xiaoxiclick
    };
  }
};